/**
 * External dependencies
 */
import classnames from 'classnames';

import { withSelect } from '@wordpress/data';
import { compose } from '@wordpress/compose';
import { InnerBlocks, __experimentalUseColors } from '@wordpress/block-editor';
import { useRef } from '@wordpress/element';

/**
 * Registers a new block provided a unique name and an object defining its behavior.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/#registering-a-block
 */
import { registerBlockType } from '@wordpress/blocks';

/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * Every block starts by registering a new block type definition.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/#registering-a-block
 */
registerBlockType( 'oleti/section', {
	/**
	 * This is the display title for your block, which can be translated with `i18n` functions.
	 * The block inserter will show this name.
	 */
	title: __( 'Section', 'oleti' ),

	/**
	 * This is a short description for your block, can be translated with `i18n` functions.
	 * It will be shown in the Block Tab in the Settings Sidebar.
	 */
	description: __( 'Section Block for Gutenberg', 'oleti' ),

	/**
	 * Blocks are grouped into categories to help users browse and discover them.
	 * The categories provided by core are `common`, `embed`, `formatting`, `layout` and `widgets`.
	 */
	category: 'layout',

	/**
	 * An icon property should be specified to make it easier to identify a block.
	 * These can be any of WordPress’ Dashicons, or a custom svg element.
	 */
	icon: 'smiley',

	/**
	 * Optional block extended support features.
	 */
	supports: {
		// Removes support for an HTML mode.
		html: false,
	},

	/**
	 * The edit function describes the structure of your block in the context of the editor.
	 * This represents what the editor will render when the block is used.
	 *
	 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
	 *
	 * @param {Object} [props] Properties passed from the editor.
	 *
	 * @return {WPElement} Element to render.
	 */
	edit( { className } ) {
		const ref = useRef();
		const { TextColor, InspectorControlsColorPanel } = __experimentalUseColors(
			[ { name: 'textColor', property: 'color' } ],
			{
				contrastCheckers: { backgroundColor: true, textColor: true },
				colorDetector: { targetRef: ref },
			},
			[]
		);
		return (
			<>
				<InspectorControls>
					<PanelBody
						title={ __( 'Spacing', 'oleti' ) }
						//initialOpen={ false }
					>
						<DimensionControl
							label={ __( 'Padding', 'oleti' ) }
							value={ attributes.paddingSize }
							onChange={ partialRight(
								updateSpacing,
								'paddingSize'
							) }
							help={ __(
								'Adjust spacing around content within the block.',
								'oleti'
							) }
						/>

						<DimensionControl
							label={ __( 'Margin', 'oleti' ) }
							value={ attributes.marginSize }
							onChange={ partialRight(
								updateSpacing,
								'marginSize'
							) }
							help={ __(
								'Adjust spacing on the sides of the block.',
								'oleti'
							) }
						/>
					</PanelBody>
				</InspectorControls>
				{ InspectorControlsColorPanel }
				<BackgroundColor>
					<TextColor>
						<div className={ className } ref={ ref }>
								<InnerBlocks
									renderAppender={
										! hasInnerBlocks &&
										InnerBlocks.ButtonBlockAppender
									}
								/>
						</div>
					</TextColor>
				</BackgroundColor>
			</>
		);
	},

	/**
	 * The save function defines the way in which the different attributes should be combined
	 * into the final markup, which is then serialized by the block editor into `post_content`.
	 *
	 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
	 *
	 * @return {WPElement} Element to render.
	 */
	save() {
		return (
			<p>
				{ __(
					'Section Block – hello from the saved content!',
					'oleti'
				) }
			</p>
		);
	},
} );
